body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.page_curtain_open {
  position: fixed;
  height: 100%; }

.news-comments-curtain {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: calc(100% - 10px);
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
  transition: background-color 0.3s, -webkit-transform 0.3s cubic-bezier(0.42, 0, 1, 1);
  transition: transform 0.3s cubic-bezier(0.42, 0, 1, 1), background-color 0.3s;
  transition: transform 0.3s cubic-bezier(0.42, 0, 1, 1), background-color 0.3s, -webkit-transform 0.3s cubic-bezier(0.42, 0, 1, 1);
  box-sizing: border-box; }
  .news-comments-curtain_mode_folded {
    -webkit-transform: translateY(calc(100% - 52px));
            transform: translateY(calc(100% - 52px)); }
  .news-comments-curtain_mode_open {
    background-color: rgba(18, 27, 73, 0.2);
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  .news-comments-curtain-container {
    position: relative;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    background-color: lightgrey; }
    .news-comments-curtain_mode_open .news-comments-curtain-container {
      border-top-left-radius: 15px;
      border-top-right-radius: 15px; }
  .news-comments-curtain-overlay {
    position: absolute;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
    transition: background-color .3s; }
    .news-comments-curtain_mode_open .news-comments-curtain-overlay {
      background-color: rgba(18, 27, 73, 0.2);
      -webkit-transform: translateY(0);
              transform: translateY(0); }
  .news-comments-curtain-header {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 52px;
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
    border-bottom: 1px solid black; }

